// MAIN SLIDER

.slider-main{
	max-width: 100vw;
	position: relative;
	height: 490px;
	&__box{
		height: 100%;
		width: 100%;
		overflow: hidden;
	}
	&__wrap{
		height: 100%;
		width: 1000%;
		display: -ms-flexbox;
		display: flex;
	}
	&__item{
		width: 100vw;
		height: 100%;
		&--link{
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
		}
		&.item-1{
			background: url('./../../images/bunner-foto-1.jpg');
			background-position: center center;
			background-size: cover;
			position: relative;
		}
	}

	&__nav{
		width: 100%;
		font-family: 'proximanowa-light';
		position: absolute;
		bottom: -45px;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: center;
		    justify-content: center;
		left: 50%;
		transform: translate(-50%);
		white-space: nowrap;
	}
	&__btn{
		color: #292A2F;
		cursor: pointer;
		vertical-align: middle;
		height: 45px;
		margin-left: 8px;
		box-sizing: border-box;
		overflow: hidden;
		border-bottom: 1px solid #cadad1;
		padding-left: 15px;
		padding-right: 15px;
		background-color: $white;
		&:hover{
			color: $red;
		}
		&.is-active{
			background-color: #eef4f2;
			border-bottom: 2px solid #4d917a;
		}
	}

	&__btn--chevron{
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		font-size: 32px;
		font-weight: 900;
		color: $white;
		cursor: pointer;
		&:hover{
			font-size: 36px;
		}
		&.btn-l{
			left: 10%;
		}
		&.btn-r{
			right: 10%;
		}
	}
}
.slider-main__links{
	position: absolute;
	//left: 125px;
	left: 22.5%;
	top: 75%;
	&>.slider-main__link{
		font-family: 'proximanowa-bold';
		font-weight: 300;
		padding: 10px 15px;
		display: inline-block;
		color: $white;
		border: 2px solid $white;
	}
}
// END -- MAIN SLIDER

