.form__input{
	font-family: 'proximanowa-regular';
	padding-left: 10px;
	width: 100%;
	height: 37px;
	border: 1px solid #9a9a9a;
	border-radius: 3px;
	&:focus{
		box-shadow: 0px 0px 5px 2px #4d917a;
	}
}
.subscribe-form--sm{
	background-color: #eef4f2;
	padding: 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	height: 250px;
}

.subscribe-form__checkbox + label {
  cursor: pointer;
  
}
.subscribe-form__checkbox:not(checked) {
  	position: absolute;
  	opacity: 0;
  	margin-top: 5px;
  	
}
.subscribe-form__checkbox:not(checked) + label {
  	position: relative;
	padding: 0 0 0 30px;
}
.subscribe-form__checkbox:not(checked) + label:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 16px;
	height: 16px;
	border-radius: 1px;
	border: 1px solid #9a9a9a;
	background: $white;
	opacity: 1;
}

.subscribe-form__checkbox:not(checked) + label:after {
	content: '';
	position: absolute;
	top: 0px;
	left: 5px;
	width: 0px;
	height: 0px;
	transition: all .1s;
	transform: rotate(45deg);
}
.subscribe-form__checkbox:checked + label:before {
	border: 1px solid #4d917a;
}
.subscribe-form__checkbox:checked + label:after {
	width: 5px;
	height: 10px;
	border-right: 3px solid #4d917a;
	border-bottom: 3px solid #4d917a;
}
.subscribe-form__checkbox:checked + label{
	color: #4d917a;
}
.subscribe-form__btn{
	width: 100%;
	background-color: $red;
	border: none;
	color: $white;
	min-height: 40px;
	margin-bottom: 10px;
	cursor: pointer;
	white-space: normal;
}

