//SOCIAL LINKS

.social-links{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	padding: 30px 165px;
	&__block{
		width: 230px;
		margin-right: 30px;
		display: -ms-flexbox;
		display: flex;
		
		&:last-child{
			margin-right: 0;
		}

		&>.icon{
			color: #000;
			width: 35px;
			height: 35px;
			font-size: 35px;
			margin-right: 15px;
			&--facebook:hover{
				color: #003390;
			}
			&--instagram:hover{
				color: #0069AC;

			}
			&--google-plus:hover{
				color: #9F1600;
			}
			&--mail:hover{
				color: #BA2B00;
			}

		}

		&>.social-link--wrap{
			font-size: 15px;
			
			&>.social-link{
				color: #4d917a;
				font-size: 16px;
				font-weight: 900;


				&:hover{
					color: #c15666;
				}
				&>.icon.icon--rss-symbol{
					font-size: 12px;
					margin-right: 5px;
				}
			}
			&>.link--bused{
				border-bottom: dashed 1px #4d917a;
				&:hover{
					border-bottom: dashed 1px #c15666
				}
			}
		}
	}
}
//END SOCIAL LINKS

